.sended_post{
    display: flex;
    align-items: center;
    margin-bottom: 19px;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    font-family: var(--roboto);
    color: black;
}
.sended_post::before{
    content: '';
    display: block;
    width: 18px;
    height: 18px;
    background: #FF5C00;
    border-radius: 50%;
    margin-right: 5px;
}
