.profile{
    margin: 14px 19px;
    display: flex;
    margin-bottom: 25px;
}
.profile img{
    width: 100px;
    height: 104px;
}
.profile_about{
    margin-left: 14px;
}

.profile_name{
    font-weight: 900;
    font-size: 12px;
    line-height: 14px;
    color: #000;
    font-family: var(--roboto);
    margin-bottom: 24px;
}
.profile_decription{
    font-size: 12px;
    line-height: 14px;
    font-weight: 300;
    font-family: var(--roboto);
    margin: 5px 0;
}

.mypost{
    margin: 0 30px;
}
.post_title{
    margin-left: 20px;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    font-family: var(--roboto);
    color: #000;
    margin-bottom: 7px;
}