.nav{
    background: #000;
    padding: 25px;
    border-radius: 10px;
    margin-right: 40px;
}

.list{
    display: flex;
    flex-direction: column;
}
.link{
    display: flex;
    color: white;
    margin: 10px 0;
    cursor: default;
}

.list li:last-child a{
    margin-top: 40px;
}

.active{
    color: #FF5C00;
    margin: 10px 0;
    cursor: default;
    display: flex;
}