@import url("https://fonts.googleapis.com/css2?family=Playfair+Display&family=Roboto:wght@300;400&display=swap");

:root {
  --roboto: "Roboto", sans-serif;
  --orange: #ff5c00;
}
.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
  position: relative;
}
.dialogs_items {
  padding: 10px;
  margin-right: 30px;
}
.dialog {
  display: block;
  margin: 10px;
  padding: 15px;
  text-align: center;
  border-radius: 15px;
  width: 150px;
  color: black;
  background: var(--orange);
  cursor: pointer;
  position: relative;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
}
.dialog.active {
  color: var(--orange);
  background: #000;
}
.messages {
  padding: 20px;
  position: relative;
  overflow-y:scroll;
}
.messages_items{
  height: 280px;
  overflow-y: scroll;
  

}
.message {
  padding: 10px;
  border-radius: 15px 0 0 0;
  margin-bottom: 40px;
  font-family: var(--roboto);
  font-style: 14px;
  font-weight: 600;
  position: relative;
  background: white;
  display: flex;
  flex-wrap: wrap;
}
.message::before {
  content: "";
  display: block;
  background: var(--orange);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  bottom: -20px;
  left: -20px;
}

.send {
  display: flex;
  margin-top: 10px;
  width: 100%;
}
.messageArea {
  resize: none;
  padding: 5px 5px 5px 5px;
  font-weight: 400;
  width: 65%;
  font-size: 14px;
  line-height: 14px;
  color: black;
  outline: none;
  border: none;
  overflow: hidden;
  line-height: 18px;
  margin-right: 10px;
}
.sendMessage{
    background: var(--orange);
    color: black;
    font-family: var(--roboto);
    font-weight: 500;
    border: none;
    outline: none;
    border-radius: 10px;
    padding: 10px;

}
