@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&family=Roboto:wght@300;400&display=swap');


:root{
    --roboto:  'Roboto', sans-serif;
}
.profile{
    margin: 14px 19px;
    display: flex;
    margin-bottom: 25px;
}
.profile img{
    width: 100px;
    height: 104px;
}
.profile_about{
    margin-left: 14px;
}

.profile_name{
    font-weight: 900;
    font-size: 12px;
    line-height: 14px;
    color: #000;
    font-family: var(--roboto);
    margin-bottom: 24px;
}
.profile_decription{
    font-size: 12px;
    line-height: 14px;
    font-weight: 300;
    font-family: var(--roboto);
    margin: 5px 0;
}

.mypost{
    margin: 0 30px;
}
.post_title{
    margin-left: 20px;
    font-weight: 900;
    font-size: 14px;
    line-height: 16px;
    font-family: var(--roboto);
    color: #000;
    margin-bottom: 7px;
}
.post{
    display: flex;
    flex-direction: column;
}

.post::after{
    content: '';
    display: block;
    width: 100%;
    border: 3px solid #D9D8D8;
    border-radius: 2px;
    margin-top: 8px;
    margin-bottom: 17px;

}
.new_post{
    height: 65px;
    resize: none;
    padding:  10px;
    border: none;
    outline: none;
    border-radius: 12px;
    background: rgba(123, 118, 118, 0.19);
    font-family: var(--roboto);
    overflow: hidden;

}
.btn{
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    outline: none;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    color: #000;
    background: #000;
    background: #FF5C00;
    border-radius: 370px;
    align-self: flex-end;
    padding: 8px 20px;
    margin-top: 7px;
    font-family: var(--roboto);
}

.sended_post{
    display: flex;
    align-items: center;
    margin-bottom: 19px;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    font-family: var(--roboto);
    color: black;
}
.sended_post::before{
    content: '';
    display: block;
    width: 18px;
    height: 18px;
    background: #FF5C00;
    border-radius: 50%;
    margin-right: 5px;
}

