@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&family=Roboto:wght@300;400&display=swap');


:root{
    --roboto:'Roboto', sans-serif;

}
.header{
    width: 100%;
    padding: 11px 31px 18px 28px;
    background: #000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.header_right{
    display: flex;
    align-items: center;
}

.form{
    display: flex;
    background: white;
    align-items: center;
    height: 30px;
    margin-left: 147px;
}
.search{
    border: none;
    outline: none;
    padding:  7px 19px;
}
.logo{
    font-weight: 400;
font-size: 32px;
line-height: 38px;
color: white;
font-family:var(--roboto);
}

.btn{
    color: #000;
    font-weight: 300;
    font-size: 13px;
    line-height: 15px;
    padding: 9px 30px;
    background: #FF5C00;
    border-radius: 370px;
    display: block;
    cursor: default;
    font-family: var(--roboto);
}